
import init from '../../init';

const html = init('box');
export default {
  template: html,
  data () {
    return {
      text: 'box'
    };
  },
  mounted () {
    console.log('box');
    setTimeout(() => {
      this.text = 'box';
    }, 2000);
  },
  methods: {

  }
};



